@import "@/assets/global.scss";

























.main {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: flex-start;

  > a {
    margin-bottom: 8px;
    width: calc((100% - 16px) / 2);

    > img {
      object-fit: contain;
      width: 100%;
      height: 100%;
      max-width: 175px;
      max-height: 76px;
    }
  }

  > a:nth-child(odd) {
    margin-right: 16px;
  }
}
